import { filter, nth, split, trim, zipObject, toNumber } from "lodash";
/**
 *
 * @param {string} path
 * @param {string} url
 * @returns {{}}
 */

function parseRouteParams(path, url) {
  var matcher = function matcher(value) {
    return /:\w+/g.test(value);
  }; //first normalize the path & url, then split into arrays


  var pathSegments = split(path, "/");
  var urlSegments = split(url, "/"); //filter the wildcard path segments & trim up the strings

  var wildcards = filter(pathSegments, function (_p) {
    return matcher(_p);
  });
  wildcards = wildcards.map(function (w) {
    return trim(w, ":");
  }); //get the index of each wildcard in the path and url

  var wildcardIndex = [];
  var urlValues = [];
  pathSegments.map(function (_p, index) {
    return matcher(_p) && wildcardIndex.push(index);
  }); //create the url values array from the wildcard indexes

  wildcardIndex.map(function (wildcard) {
    return urlValues.push(nth(urlSegments, wildcard));
  }); //convert any number strings to the correct type

  urlValues = urlValues.map(function (val) {
    if (!isNaN(val)) {
      return toNumber(val);
    }

    return val;
  }); //create an object keyed by path wildcard & associated path string as the value

  var params = zipObject(wildcards, urlValues);
  return params;
}

export default parseRouteParams;